.bar-container {
  background-color: #e0004d34;
  position: relative;
  align-items: center;
  justify-content: center;
  display: flex;
}
.bar {
  height: 2px;
  width: 100%;
  background-color: #e0004d;
  animation-name: barpulse;
  animation-duration: 0.7s;
  animation-iteration-count: infinite;
  animation-direction: forward;
}

@keyframes barpulse {
  0% {
    opacity: 1;
    transform: scale(0.1, 1);
  }
  70% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
    transform: scale(1, 1);
  }
}
